import React from "react";
import { Container, Typography, Button, Grid } from "@material-ui/core";
import useStyles from './styles';
import CardItem from './CartItem/CartItem';
import { Link } from 'react-router-dom';

const Cart = ({ cart, handleUpdateCartQty, handleRemoveFromCart, handleEmptyCart }) => {

  
  const classes = useStyles();

  const EmptyCart = () => (
    <Typography variant="subtitle1">
      Немате производи во кошничката, 
      <Link to='/' className={classes.link}>почнете со додавање</Link>!
    </Typography>
  );

  const FilledCart = () => (
    <>
      <Grid container spacing={3}>
        {cart.line_items.map((item) => (
          <Grid item xs={12} sm={4} key={item.id}>
            <CardItem item={item} onUpdateCartQty={handleUpdateCartQty} onRemoveFromCart={handleRemoveFromCart}/>
          </Grid>
        ))}
      </Grid>
      <div className={classes.cardDetails}> 
      <Typography variant='h4'>
          Вкупно: {cart.subtotal.formatted} ден.
          </Typography> 
          <div>
              <Button className={classes.emptyButton} size="large" type="button" variant="contained" color="secondary" onClick={handleEmptyCart}>Празна Кошничка</Button>
              <Button component={Link} to="/checkout" className={classes.checkoutButton} size="large" type="button" variant="contained" color="primary">Кон наплата</Button>
          </div>

          </div>
    </>
  );

  if(!cart.line_items) return 'Се вчитува...';
  return (
    <Container>
      <div className={classes.toolbar} />
      <Typography className={classes.title} variant='h3' gutterBottom>Вашата Кошничка</Typography>
      {!cart.line_items.length ? <EmptyCart /> : <FilledCart />}
    </Container>
  );
};

export default Cart;
